export default {
    name: 'app-reservation-form',
    props: {
        item: {
            type: Object,
            default() {
                return {}
            }
        }
    },
    methods: {
        refund(data) {
            this.$emit('click', data, this.item);
        },
        evaluation() {
            this.$jump({
                open_type: 'navigate',
                url: `pages/order/appraise/appraise?id=${this.item.id}`
            })
        }
    }
}
